.search-form{
    height: 50px;
    display: flex;
    justify-content: center;
    margin: 15px 0 15px 0;
    background-color: #007474;
    align-items: center;
    border-radius: 8px;
}

.search-form input{ 
    height: 30px;
    margin-right: 15px;
    align-items: center;
    border-radius: 5px;
    border: none;
    text-align: center;

}
.search-form button{
    height: 25px;
    width: 100px;
    border: none;
    border-radius: 5px;
    &:hover {
        background-color: rgb(141, 192, 179);
    }
}

.gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;

}

.photo-card{
    width: 400px;
    height: 330px;
    margin-bottom: 20px;
    box-shadow: 0px 0px 13px 10px rgba(0, 0, 0, 0.13);
    border-radius: 6px;
    overflow: hidden;
}

.imgThumb{
    display: flex;
    width: 400px;
    height: 270px;
    
}

.linkImg {
    width: 400px;
    height: 270px;
    // & :hover.img{
        
        
    // }
}

.img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.info{
    display: flex;
    justify-content: space-around;
    text-align: center;
}

.load-more {
   display: flex;
   margin: auto;
}